import * as types from './types';

export function getBlogs(page, search, orderBy) {
    return { type: types.GET_BLOGS, page, search, orderBy }
}

export function getBlog(id) {
    return { type: types.GET_BLOG, id }
}

export function createBlog(data) {
    return { type: types.CREATE_BLOG, data }
}

export function createBlogs() {
    return { type: types.CREATE_BLOGS }
}

export function removeBlog(id) {
    return { type: types.REMOVE_BLOG, id }
}

export function removeBlogs(data) {
    return { type: types.REMOVE_BLOGS, data }
}

export function updateBlog(data, id) {
    return { type: types.UPDATE_BLOG, data, id }
}