import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { removeBlog } from '../../actions/blogActions';
class BlogItem extends Component {
    constructor() {
        super();
        this.state = {

        };
        this.onDeleteItem = this.onDeleteItem.bind(this);
    }
    onDeleteItem(id) {
        if (window.confirm('Are you sure you wish to delete this item?')) {
            this.props.removeBlog(id);
        }
    }
    handleClick = (event) => {
        this.props.handleClickParent(event.currentTarget.dataset.id);
    }
    render() {
        const { blog } = this.props;
        return (
            <tr>
                <td className="center">
                    <label className="pos-rel">
                        <input type="checkbox" onClick={this.handleClick} data-id={blog.id} className="ace" />
                        <span className="lbl"></span>
                    </label>
                </td>
                <td>{blog.name}</td>
                <td>{blog.blog_categories ? blog.blog_categories.title : 'Cha c danh mc'}</td>
                <td>{blog.status ? ' kch hot' : 'Cha kch hot'}</td>
                <td>{blog.users.name}</td>
                <td>
                    <div className="btn-group">
                        <Link to={{ pathname: `/blogs/${blog.id}` }} className="btn btn-xs btn-info">
                            <i className="ace-icon fa fa-pencil bigger-120"></i>
                        </Link>

                        <button className="btn btn-xs btn-danger" onClick={() => this.onDeleteItem(blog.id)}>
                            <i className="ace-icon fa fa-trash-o bigger-120"></i>
                        </button>
                    </div>
                </td>
            </tr>
        )
    }
}
BlogItem.propTypes = {
    blog: PropTypes.object.isRequired,
    removeBlog: PropTypes.func.isRequired
}

const mapStateToProps = (state) => ({

});
export default connect(mapStateToProps, { removeBlog })(BlogItem);
