import { all, call, put, takeLatest, actionChannel, take } from 'redux-saga/effects';
import RequestService from '../utils/requestService';
import Message from '../utils/notificationMessage';
import * as types from '../actions/types';

function* setCategoriesLoading() {
    yield put({ type: types.CATEGORIES_LOADING });
}

function* fetchCategories() {
    try {
        const result = yield RequestService.get('/api/blog_categories');
        yield put({ type: types.SET_TREE, payload: result.data.data.data });
        yield put({ type: types.SET_CATEGORIES, payload: result.data.data.all });
    } catch (error) {
        console.log(error.response);
    }
}

export function* blogCategoryWatcher() {
    const subChannel = yield actionChannel(types.GET_CATEGORIES);
    while (true) {
        const action = yield take(subChannel);
        yield call(fetchCategories, action);
    }
}

function* clearError() {
    yield put({ type: types.CLEAR_ERRORS })
}

function* createCategory(data) {
    yield call(clearError);
    yield call(setCategoriesLoading);
    try {
        const result = yield RequestService.post('/api/blog_categories', data.data);
        Message.displaySuccess(result.data.message);
        yield put({ type: types.CREATED_CATEGORY, payload: result.data.data });
    } catch (error) {
        yield call(setCategoriesLoading);
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
            if (error.response.data.data) {
                yield put({ type: types.GET_ERRORS, payload: error.response.data.data });
            }
        }
    }
}

function* updateCategory(data) {
    try {
        yield RequestService.put('/api/blog_categories/' + data.node.id, data.nextParentNode);
    } catch (error) {
        console.log(error.response);
    }
}

function* removeCategory(data) {
    try {
        yield RequestService.del('/api/blog_categories/' + data.data);
    } catch (error) {
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
        }
    }
}

export function* categorySaga() {
    yield all([
        takeLatest(types.CREATE_CATEGORY, createCategory),
        takeLatest(types.UPDATE_CATEGORY, updateCategory),
        takeLatest(types.REMOVE_CATEGORY, removeCategory)
    ])
}