import {
    CREATED_CATEGORY,
    SET_CATEGORIES,
    CATEGORIES_LOADING,
    SET_TREE
} from "../actions/types";

const initialState = {
    blog_categories: [],
    tree: [],
    loading: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case CATEGORIES_LOADING:
            return {
                ...state,
                loading: !state.loading
            }
        case SET_CATEGORIES:
            return {
                ...state,
                blog_categories: action.payload,
                loading: false
            }
        case SET_TREE:
            return {
                ...state,
                tree: action.payload,
                loading: false
            }
        case CREATED_CATEGORY:
            return {
                ...state,
                loading: false,
                blog_categories: [...state.blog_categories, action.payload]
            }
        default:
            return state;
    }
}