import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Breadcrumb from '../main/Breadcrumb';
import { getCategories } from '../../actions/categoryActions';
import { getBlog, updateBlog } from '../../actions/blogActions';
import { Link } from 'react-router-dom';
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import isEmpty from '../../utils/isEmpty';
import SelectForm from '../form/SelectForm';
import TextareaForm from '../form/TextareaForm';
import InputForm from '../form/InputForm';
import slugify from 'slugify';
class BlogDetail extends Component {
    constructor() {
        super();
        this.state = {
            id_category: '',
            name: '',
            alias: '',
            image: '',
            content: '',
            meta_description: '',
            description: '',
            status: false,
            errors: {}
        };
        this.onStatus = this.onStatus.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.handleEditorChange = this.handleEditorChange.bind(this);
    }
    onStatus() {
        this.setState({
            status: !this.state.status
        })
    }
    onChange(e) {
        if (e.target.name === 'name') {
            this.setState({
                alias: slugify(e.target.value)
            })
        }
        this.setState({ [e.target.name]: e.target.value });
    }
    componentDidMount() {
        this.props.getCategories();
        this.props.getBlog(this.props.match.params.id);
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
        }
        if (nextProps.blogs.blog) {
            const detail = nextProps.blogs.blog;
            detail.id_category = !isEmpty(detail.id_category) ? detail.id_category : '';
            detail.name = !isEmpty(detail.name) ? detail.name : '';
            detail.alias = !isEmpty(detail.alias) ? detail.alias : '';
            detail.image = !isEmpty(detail.image) ? detail.image : '';
            detail.content = !isEmpty(detail.content) ? detail.content : '';
            detail.meta_description = !isEmpty(detail.meta_description) ? detail.meta_description : '';
            detail.description = !isEmpty(detail.description) ? detail.description : '';
            detail.status = detail.status ? true : false;
            this.setState({
                id_category: detail.id_category,
                name: detail.name,
                alias: detail.alias,
                image: detail.image,
                content: detail.content,
                meta_description: detail.meta_description,
                description: detail.description,
                status: detail.status
            })
        }
    }
    handleEditorChange() {
        return (event, editor) => {
            this.setState({ content: editor.getData() });
        }
    }
    onSubmit(e) {
        e.preventDefault();
        const blogData = {
            id_category: this.state.id_category,
            name: this.state.name,
            alias: this.state.alias,
            image: this.state.image,
            content: this.state.content,
            meta_description: this.state.meta_description,
            description: this.state.description,
            status: this.state.status,
            id: this.props.match.params.id
        }

        this.props.updateBlog(blogData, this.props.match.params.id);
    }
    render() {
        const { errors } = this.state;
        const { blog_categories } = this.props.blog_categories;
        const { loading } = this.props.blogs;
        let blogsLoading;
        if (loading) {
            blogsLoading = <h1>Loading</h1>
        } else {
            blogsLoading = (
                <div>
                    <button className="btn btn-info" type="submit">
                        <i className="ace-icon fa fa-check bigger-110"></i>
                        Sa mi
                    </button>
                    <Link to="/blogs" className="btn btn-warning" role="button">
                        <i className="ace-icon fa fa-close bigger-110"></i>
                        Hy
                    </Link>
                </div>
            )
        }
        return (
            <div className="main-content-inner">
                <Breadcrumb title="Add blog" link="/blogs" previous="Blog" />
                <div className="page-content">
                    <div className="page-header">
                        <h1>{this.state.name}</h1>
                    </div>
                    <div className="row">
                        <div className="col-xs-12">
                            <form className="form-horizontal" onSubmit={this.onSubmit}>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <InputForm
                                            name="name"
                                            placeholder="Name"
                                            value={this.state.name}
                                            onChange={this.onChange}
                                            error={errors.name}
                                            title="Name"
                                        />
                                        <InputForm
                                            name="alias"
                                            placeholder="Alias"
                                            value={this.state.alias}
                                            onChange={this.onChange}
                                            error={errors.alias}
                                            title="SEO Meta"
                                            disabled="disabled"
                                        />
                                        {isEmpty(blog_categories) ? null : <SelectForm
                                            label="Chuyn mc"
                                            name="id_category"
                                            value={this.state.id_category}
                                            onChange={this.onChange}
                                            error={errors.id_category}
                                            options={blog_categories}
                                        />}
                                        <TextareaForm
                                            title="Description"
                                            name="description"
                                            value={this.state.description}
                                            onChange={this.onChange}
                                            error={errors.description}
                                        />
                                        <div className="form-group">
                                            <label className="col-sm-2 control-label no-padding-right">Ni dung</label>
                                            <div className="col-sm-10">
                                                <CKEditor
                                                    editor={ClassicEditor}
                                                    data={this.state.content}
                                                    onChange={this.handleEditorChange()}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-sm-4 form-actions">
                                        <div className="form-group">
                                            <label className="col-sm-2 control-label no-padding-right">Kch hot</label>
                                            <div className="col-sm-10">
                                                {this.state.status ? (
                                                    <input type="checkbox" className="ace ace-switch ace-switch-4" onClick={this.onStatus} defaultChecked />
                                                ) : (
                                                        <input type="checkbox" className="ace ace-switch ace-switch-4" onClick={this.onStatus} />
                                                    )}
                                                <span className="lbl middle"></span>
                                            </div>
                                        </div>
                                        <TextareaForm
                                            title="Meta description"
                                            name="meta_description"
                                            value={this.state.meta_description}
                                            onChange={this.onChange}
                                            error={errors.meta_description}
                                        />
                                        <InputForm
                                            name="image"
                                            placeholder="Add url image"
                                            value={this.state.image}
                                            onChange={this.onChange}
                                            error={errors.image}
                                            title="Image"
                                        />
                                    </div>
                                </div>
                                <div className="clearfix form-actions">
                                    <div className="col-md-offset-3 col-md-9">
                                        {blogsLoading}
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
BlogDetail.propTypes = {
    errors: PropTypes.object.isRequired,
    getCategories: PropTypes.func.isRequired,
    blog_categories: PropTypes.object.isRequired,
    blogs: PropTypes.object.isRequired,
}

const mapStateToProps = (state) => ({
    blog_categories: state.blog_categories,
    blogs: state.blogs,
    errors: state.errors,
});
export default connect(mapStateToProps, { getCategories, getBlog, updateBlog })(BlogDetail);
