import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux';
import SortableTree, { removeNodeAtPath } from 'react-sortable-tree';
import { updateCategory, removeCategory } from '../../actions/categoryActions';
import 'react-sortable-tree/style.css';
class BlogCategoryFeed extends Component {
    constructor(props) {
        super(props);

        this.state = {
            treeData: [],
        };
        this.onDeleteNode = this.onDeleteNode.bind(this);
    }
    onDeleteNode(rowInfo) {
        this.props.removeCategory(rowInfo.node.id);
        if (rowInfo.node.children.length === 0) {
            let { path } = rowInfo;
            this.setState({
                treeData: removeNodeAtPath({
                    treeData: this.state.treeData,
                    path: path,
                    getNodeKey: ({ treeIndex: number }) => {
                        return number;
                    }
                })
            })
        }

    }
    componentDidMount() {
        this.setState({
            treeData: this.props.tree
        })
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            treeData: nextProps.tree
        })
    }
    render() {
        return (
            <div style={{ height: 400 }}>
                <SortableTree
                    generateNodeProps={rowInfo => ({
                        buttons: [
                            <button
                                style={{
                                    verticalAlign: 'middle',
                                }}
                                onClick={(e) => this.onDeleteNode(rowInfo)}
                            >
                                X
                            </button>
                        ],
                    })}
                    treeData={this.state.treeData}
                    onChange={treeData => this.setState({ treeData })}
                    onMoveNode={treeData => this.props.updateCategory(treeData.nextParentNode, treeData.node)}
                />
            </div>
        )
    }
}

BlogCategoryFeed.propTypes = {
    updateCategory: PropTypes.func.isRequired,
    removeCategory: PropTypes.func.isRequired,
    tree: PropTypes.array.isRequired
}
const mapStateToProps = (state) => ({

});
export default connect(mapStateToProps, { updateCategory, removeCategory })(BlogCategoryFeed);