import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Breadcrumb from '../main/Breadcrumb';
import { getCategories } from '../../actions/categoryActions';
import BlogCategoryFeed from './BlogCategoryFeed';
import BlogCategoryForm from './BlogCategoryForm';
import isEmpty from '../../utils/isEmpty';
class BlogCategory extends Component {
    componentDidMount() {
        this.props.getCategories();
    }
    render() {
        const { tree } = this.props.blog_categories;
        return (
            <div className="main-content-inner">
                <div className="breadcrumbs ace-save-state" id="breadcrumbs">
                    <Breadcrumb title="Blog category" />
                </div>

                <div className="page-content" v-if="isLoading === false">
                    <div className="row">
                        <div className="col-xs-12">
                            <div className="row">
                                <div className="vspace-16-sm"></div>
                                <div className="col-sm-6">
                                    <BlogCategoryForm />
                                </div>
                                <div className="col-sm-6">
                                    {isEmpty(tree) ? null : <BlogCategoryFeed tree={tree} />}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
BlogCategory.propTypes = {
    getCategories: PropTypes.func.isRequired,
    blog_categories: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    blog_categories: state.blog_categories
});
export default connect(mapStateToProps, { getCategories })(BlogCategory);