import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import BlogItem from './BlogItem';
class BlogFeed extends Component {
    callbackHandlerFunction = (value) => {
        this.props.handleClickBlog(value);
    }
    render() {
        const { blogs } = this.props;
        return blogs.map(blog => <BlogItem blog={blog} key={blog.id} handleClickParent={this.callbackHandlerFunction} />);
    }
}
BlogFeed.propTypes = {
    blogs: PropTypes.array.isRequired
}

const mapStateToProps = (state) => ({

});
export default connect(mapStateToProps, null)(BlogFeed);
