import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import slugify from 'slugify';
import InputForm from '../form/InputForm';
import { createCategory } from '../../actions/categoryActions';
class BlogCategoryForm extends Component {
    constructor() {
        super();
        this.state = {
            title: '',
            alias: '',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        if (e.target.name === 'title') {
            this.setState({
                alias: slugify(e.target.value),
                title: e.target.value
            })
        }
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
            this.props.blog_categories.loading = false;
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const categoryData = {
            title: this.state.title,
            alias: this.state.alias
        }
        this.setState({
            title: '',
            alias: ''
        })

        this.props.createCategory(categoryData);
    }
    render() {
        const { errors } = this.state;
        const { loading } = this.props.blog_categories;
        let categoriesLoading;
        if (loading) {
            categoriesLoading = <h1>Loading</h1>
        } else {
            categoriesLoading = (
                <div className="clearfix form-actions">
                    <div className="col-md-offset-3 col-md-9">
                        <button className="btn btn-info" type="submit">
                            <i className="ace-icon fa fa-check bigger-110"></i>
                            Submit
                    </button>
                    </div>
                </div>
            )
        }
        return (
            <form className="form-horizontal" onSubmit={this.onSubmit}>
                <div className="row">
                    <div className="col-sm-12">
                        <InputForm
                            name="title"
                            placeholder="Title"
                            value={this.state.title}
                            onChange={this.onChange}
                            error={errors.title}
                            title="Title"
                        />
                        <InputForm
                            name="alias"
                            placeholder="Alias"
                            value={this.state.alias}
                            onChange={this.onChange}
                            error={errors.alias}
                            title="SEO Meta"
                            disabled="disabled"
                        />
                    </div>
                </div>
                {categoriesLoading}
            </form>
        )
    }
}
BlogCategoryForm.propTypes = {
    errors: PropTypes.object.isRequired,
    blog_categories: PropTypes.object.isRequired,
}

const mapStateToProps = (state) => ({
    errors: state.errors,
    blog_categories: state.blog_categories
});

export default connect(mapStateToProps, { createCategory })(BlogCategoryForm);
