import { all, call, put, takeLatest, actionChannel, take } from 'redux-saga/effects';
import RequestService from '../utils/requestService';
import Message from '../utils/notificationMessage';
import history from '../utils/history';
import * as types from '../actions/types';

function* setBlogsLoading() {
    yield put({ type: types.BLOGS_LOADING });
}

function* fetchBlogs(action) {
    const { page, search, orderBy } = action;
    try {
        const result = yield RequestService.get(`/api/blogs?search=${search}&orderBy=${orderBy}&page=${page}`);
        yield put({ type: types.SET_BLOGS, payload: result.data.data });
    } catch (error) {
        console.log(error.response);
    }
}

export function* blogDetailWatcher() {
    const subChannel = yield actionChannel(types.GET_BLOG);
    while (true) {
        const action = yield take(subChannel);
        yield call(fetchBlog, action);
    }
}

function* fetchBlog(action) {
    const { id } = action;
    try {
        const result = yield RequestService.get(`/api/blogs/${id}`);
        yield put({ type: types.SET_BLOG, payload: result.data.data });
    } catch (error) {
        console.log(error.response);
    }
}

export function* blogWatcher() {
    const subChannel = yield actionChannel(types.GET_BLOGS);
    while (true) {
        const action = yield take(subChannel);
        yield call(fetchBlogs, action);
    }
}

function* clearError() {
    yield put({ type: types.CLEAR_ERRORS })
}

function* createBlog(data) {
    yield call(clearError);
    yield call(setBlogsLoading);
    try {
        const result = yield RequestService.post('/api/blogs', data.data);
        Message.displaySuccess(result.data.message);
        yield call(history.push, '/blogs');
    } catch (error) {
        yield call(setBlogsLoading);
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
            if (error.response.data.data) {
                yield put({ type: types.GET_ERRORS, payload: error.response.data.data });
            }
        }
    }
}

function* createBlogs() {
    try {
        const result = yield RequestService.get('/api/blogs/seed');
        Message.displaySuccess(result.data.message);
        yield call(history.push, '/blogs');
    } catch (error) {
        console.log(error.response);
    }
}

function* updateBlog(data) {
    yield call(clearError);
    yield call(setBlogsLoading);
    try {
        const result = yield RequestService.put('/api/blogs/' + data.id, data.data);
        Message.displaySuccess(result.data.message);
        yield call(history.push, '/blogs');
    } catch (error) {
        yield call(setBlogsLoading);
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
            if (error.response.data.data) {
                yield put({ type: types.GET_ERRORS, payload: error.response.data.data });
            }
        }
    }
}

function* removeBlog(data) {
    try {
        const result = yield RequestService.del('/api/blogs/' + data.id);
        Message.displaySuccess(result.data.message);
        const value = {
            page: 1,
            search: '',
            orderBy: 'ASC'
        }
        yield call(fetchBlogs, value);
    } catch (error) {
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
        }
    }
}

function* removeBlogs(data) {
    try {
        const result = yield RequestService.post('/api/blogs/remove', data.data);
        Message.displaySuccess(result.data.message);
        const value = {
            page: 1,
            search: '',
            orderBy: 'ASC'
        }
        yield call(fetchBlogs, value);
    } catch (error) {
        if (error.response.data.message) {
            Message.displayError(error.response.data.message);
        }
    }
}

export function* blogSaga() {
    yield all([
        takeLatest(types.CREATE_BLOG, createBlog),
        takeLatest(types.CREATE_BLOGS, createBlogs),
        takeLatest(types.REMOVE_BLOG, removeBlog),
        takeLatest(types.UPDATE_BLOG, updateBlog),
        takeLatest(types.REMOVE_BLOGS, removeBlogs)
    ])
}