import * as types from './types';

export function getCategories() {
    return { type: types.GET_CATEGORIES };
}

export function createCategory(data) {
    return { type: types.CREATE_CATEGORY, data }
}

export function removeCategory(data) {
    return { type: types.REMOVE_CATEGORY, data }
}

export function updateCategory(nextParentNode, node) {
    return { type: types.UPDATE_CATEGORY, nextParentNode, node }
}