import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Breadcrumb from '../main/Breadcrumb';
import { createBlogs, getBlogs, removeBlogs } from '../../actions/blogActions';
import BlogFeed from './BlogFeed';
import ReactPaginate from 'react-paginate';
import { Link } from 'react-router-dom';
import isEmpty from '../../utils/isEmpty';

class Blog extends Component {
    constructor() {
        super();
        this.state = {
            search: '',
            page: 1,
            orderBy: 'ASC',
            items: []
        };
        this.onChangePage = this.onChangePage.bind(this);
        this.onChangeSearch = this.onChangeSearch.bind(this);
        this.onSort = this.onSort.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onCreateSeedData = this.onCreateSeedData.bind(this);
        this.onRemoveItems = this.onRemoveItems.bind(this);
    }
    onCreateSeedData() {
        this.props.createBlogs();
        this.props.history.push('/');
    }
    onSort() {
        let orderBy;
        if (this.state.orderBy === 'ASC') {
            orderBy = 'DESC';
            this.setState({ orderBy: 'DESC' })
        } else {
            orderBy = 'ASC';
            this.setState({ orderBy: 'ASC' })
        }
        const { page, search } = this.state;
        this.props.getBlogs(page, search, orderBy);
    }
    onChangePage(pages) {
        const { search, orderBy } = this.state;
        this.props.getBlogs(pages, search, orderBy);
    }
    onChangeSearch(e) {
        this.setState({
            search: e.target.value
        });
    }
    onRemoveItems() {
        if (window.confirm('Are you sure you wish to delete this item?')) {
            this.props.removeBlogs(this.state.items);
        }
    }
    onSubmit(e) {
        e.preventDefault();
        const { page, search, orderBy } = this.state;
        this.props.getBlogs(page, search, orderBy);
    }
    componentDidMount() {
        const { page, search, orderBy } = this.state;
        this.props.getBlogs(page, search, orderBy);
    }
    callbackBlogHandlerFunction = (value) => {
        this.setState({
            items: [...this.state.items, value]
        });
    }
    render() {
        const { blogs } = this.props.blogs;
        return (
            <div>
                <div className="main-content-inner">
                    <div className="breadcrumbs ace-save-state" id="breadcrumbs">
                        <Breadcrumb name="Blog" />
                    </div>
                    <div className="page-content">
                        <div className="widget-box">
                            <div className="widget-header">
                                <h4 className="widget-title">Qun l blog</h4>
                                <div className="widget-toolbar no-border">
                                    <Link to="/blogs/add" className="btn btn-sm btn-info">
                                        <i className="ace-icon fa fa-plus"></i> Thm mi blog
                                    </Link>
                                    <a href="#!" onClick={() => this.onCreateSeedData()} className="btn btn-sm btn-warning">
                                        <i className="ace-icon fa fa-plus"></i> Thm d liu mu
                                    </a>
                                </div>
                            </div>
                            <div className="widget-body">
                                <div className="widget-main">
                                    <div className="search-active-form">
                                        <div className="form-search pull-left">
                                            <form className="form-inline" onSubmit={this.onSubmit}>
                                                <input type="text" placeholder="Search ..." className="nav-search-input" onChange={this.onChangeSearch} />
                                                <input className="btn btn-xs" type="submit" value="Tm kim" />
                                            </form>
                                        </div>

                                        <div className="pageSizebox pull-right">
                                            <div className="pageSizeNumber">
                                                Tng s bn ghi: {blogs.total ? blogs.total : null}
                                            </div>
                                        </div>
                                    </div>
                                    <table id="simple-table" className="table table-bordered table-hover">
                                        <thead>
                                            <tr>
                                                <th className="center" width="5%">

                                                </th>
                                                <th width="30%">Name <i onClick={() => this.onSort()} className="fa fa-sort"></i></th>
                                                <th width="25%">Category</th>
                                                <th width="10%">Status</th>
                                                <th width="20%">Author</th>
                                                <th width="10%">Custom</th>
                                            </tr>
                                        </thead>

                                        <tbody>
                                            {blogs.data ? <BlogFeed blogs={blogs.data} handleClickBlog={this.callbackBlogHandlerFunction} /> : null}
                                        </tbody>
                                    </table>
                                </div>
                                <div className="widget-toolbox padding-8 clearfix">
                                    <button className="btn btn-xs btn-danger pull-left" onClick={() => this.onRemoveItems()} disabled={isEmpty(this.state.items)}>
                                        <i className="ace-icon fa fa-trash-o"></i>
                                        <span className="bigger-110">Xa</span>
                                    </button>
                                    <ReactPaginate
                                        pageCount={blogs.last_page}
                                        pageRangeDisplayed={1}
                                        activeClassName="active"
                                        containerClassName="pagination pull-right page-list"
                                        onPageChange={(event) => this.onChangePage(event.selected + 1)}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
Blog.propTypes = {
    createBlogs: PropTypes.func.isRequired,
    getBlogs: PropTypes.func.isRequired,
    blogs: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    blogs: state.blogs
});
export default connect(mapStateToProps, { createBlogs, getBlogs, removeBlogs })(Blog);
